html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #cdcec8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

.propiedades__imagen {
  width: 10rem;
}

h1, h2, h3 {
  font-family: sans-serif;
  margin: 1rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.7rem;
  margin: 0;
  letter-spacing: 2px;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}

h2 {
  font-size: 3.4rem;
  margin-top: 5px;
  letter-spacing: 1px;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

main {
  background-color: s;
}

.seccion {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.flex-row-rev {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
@media (max-width: 768px) {
  .flex-row-rev {
    flex-direction: column;
  }
}

.boton-amarillo {
  background-color: #E08709;
  color: #ffffff; /* Blanco para el texto */
  font-weight: bold;
  text-decoration: none;
  padding: 0.5rem 3rem;
  text-align: center;
  margin-top: 1rem;
  display: inline-block;
  margin: 0.5rem;
  border: none;
  border-radius: 4px; /* Añadido para un borde más moderno */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Sombra para un efecto más moderno */
  transition: background-color 0.3s ease, box-shadow 0.3s ease; /* Transiciones suaves */
}
.boton-amarillo:hover {
  background-color: #cc7b08;
  cursor: pointer;
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15); /* Sombra más pronunciada al pasar el ratón */
}

