.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p, .dark-mode a, .dark-mode blockquote, .dark-mode tr, .dark-mode li {
  color: white;
}
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: gray;
}
.dark-mode .horarios__numero {
  color: gray;
}
.dark-mode .horario__alumno a {
  color: #FFFFFF;
}
.dark-mode .horario__borrar {
  background-color: #1a1a1a;
}
.dark-mode .login {
  background-color: #333333;
}
.dark-mode #añadir-cancion textarea {
  background-color: #333333;
}

.horarios__titulo {
  display: flex;
  justify-content: space-around;
  margin: 2rem 0;
}
.horarios__titulo p {
  font-size: 5rem;
  padding: 0%;
  margin: 0%;
}
@media (max-width: 768px) {
  .horarios__titulo p {
    font-size: 3rem;
  }
}

.horarios__fila {
  display: grid;
  grid-template-columns: auto 1fr; /* El primer elemento (número) tiene tamaño automático, el resto ocupa el espacio restante */
  grid-template-rows: 1fr 1fr; /* Dos filas de igual tamaño para los alumnos */
  align-items: stretch;
}

.horarios__numero {
  color: #FFFFFF;
  grid-row: span 2; /* Ocupa ambas filas */
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 6rem; /* Ajusta según sea necesario */
  background-color: #333333; /* Color de fondo opcional */
  padding: 0 5rem;
  margin: 0;
}

.horario__alumno {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #ccc; /* Bordes opcionales */
}
.horario__alumno a {
  list-style: none;
  color: #000000;
}
.horario__alumno p {
  padding: 0 5rem;
  text-align: center;
}
@media (max-width: 768px) {
  .horario__alumno p {
    grid-column: 1/-1; /* Espacio entre el <p> y los botones */
  }
}
@media (max-width: 768px) {
  .horario__alumno .presente {
    grid-column: 2/3;
  }
}
.horario__alumno .presente:disabled {
  background-color: #333333;
  color: #c6c7c0;
}
@media (max-width: 768px) {
  .horario__alumno {
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* 4 columnas iguales */
    gap: 1rem; /* Espacio entre columnas */
  }
}

.horario__borrar {
  border: none;
  background-color: #cdcec8;
  grid-column: 3/4;
}
.horario__borrar svg {
  width: 5rem;
}

@media (max-width: 768px) {
  .contenedor {
    margin: 0;
  }
}

