header {
  text-align: center;
  padding: 20px;
  background-color: #333333;
  color: #FFFFFF;
  display: flex;
  justify-content: space-between;
}
header .darkButton {
  width: 5rem;
  height: 5rem;
}

.subtitulo {
  font-size: 1.5rem;
  margin-top: 5px;
  letter-spacing: 1px;
}

.nombre {
  justify-content: space-between;
}
.nombre h1 {
  font-weight: 500;
  font-size: 5rem;
}
@media (max-width: 768px) {
  .nombre h1 {
    font-size: 3rem;
  }
}

.canciones {
  border: 0.3rem solid #333333;
  border-radius: 5rem;
  margin: 1rem;
}
.canciones h1 {
  text-align: left;
  margin-left: 3rem;
  font-weight: bold;
  font-size: 3rem;
}
@media (max-width: 768px) {
  .canciones h1 {
    font-size: 2rem;
  }
}
.canciones ul {
  list-style: none;
}
.canciones ul li {
  border-bottom: 1px solid #333333;
  margin: 1rem;
}
.canciones p {
  margin: 4rem;
}
.canciones #añadir-cancion {
  display: flex;
  justify-content: center;
  align-items: center;
}
.canciones textarea {
  background-color: #cdcec8;
  color: #FFFFFF;
  border: 0.1rem solid #379cad;
  width: 50rem;
  height: 4rem;
  margin: 1rem;
  text-align: center;
  padding: 0.5rem;
}

.login {
  margin: 20rem 2rem;
  padding: 2rem;
  justify-content: space-between;
  background-color: #b4b6ad;
}
@media (max-width: 768px) {
  .login {
    margin: 10rem 1rem;
  }
  .login h1 {
    font-size: 2.5;
  }
}